img.detail-img {
  min-width: 400px;
  max-width: 48%;
  outline: hsla(176, 69%, 61%, 0.453);
  outline-style: solid;
  outline-width: 0.1rem;
  margin-top: 2rem;
  margin-bottom: 1rem;
  margin-left: 1rem;
  margin-right: 1rem;
  box-shadow: 0px 0px 4px 6px #fafafaaa;
}

.movie-details {
  margin-top: 0.2vh;
  margin-bottom: 5rem;
  margin-left: 1vw;
  margin-right: 1vw;
  min-width: 510px;
  max-width: 95vw;
  background-blend-mode: lighten;
}

.movie-title {
  color: hsl(355, 53%, 40%);
  font-weight: 800;
  font-size: 3rem;
  text-shadow: rgba(0, 25, 25, 0.726) 0rem 0.125rem 0.25rem;
  letter-spacing: 0.005rem;
}

.similar-movies {
  margin-top: 5rem;
  max-width: 85vw;
}

